import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			array: [ '女', '男',],
			index: 1,
			config: {
				type: 0, //0无title，1有title
				title: 'TA的资料',
				back: true
			},
			userInfo: {
				name: '',
				image: '',
				phone: '',
				sex: '',
				profile: ''
			},
			userId:'',
		}
	},
	components: {
		navigation,
	},
	onLoad(options) {
		this.userId = options.userId
	},
	onShow(){
		this.getuser()
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		getuser(){
			this.$http.get('/api/person/info/', {
				userId: this.userId
			}).then(res => {
				this.userInfo = res
			}).catch((e) => {});
		},
	}
}
